@import url(//cdn.quilljs.com/1.3.6/quill.core.css);
* {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: middle;
  color: #212121;
  font: inherit;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
picture,
section {
  display: block;
}
@font-face {
  font-family: 'NotoSansKR';
  font-style: normal;
  font-weight: 350;
  src: local('NotoSansKR-DemiLight'), url(/static/fonts/NotoSansKR-DemiLight.9ab2a64.woff2) format('woff2'), url(/static/fonts/NotoSansKR-DemiLightl.0dfdcea.woff) format('woff');
  font-stretch: normal;
  unicode-range: U+4E00-9F95, U+F900-FA0B;
}
@font-face {
  font-family: 'ns';
  font-style: normal;
  font-weight: 300;
  src: local('NanumSquare'), url(/static/fonts/NanumSquare.acba619.woff) format('woff');
  font-stretch: normal;
}
html {
  font-family: 'ns', 'malgun gothic', sans-serif;
  /* 1 */
  -ms-text-size-adjust: 100%;
  /* 2 */
  -webkit-text-size-adjust: 100%;
  overflow-x: hidden;
  /* 2 */
}
body {
  line-height: 1.2;
  font-size: 13px;
  letter-spacing: 0em;
  overflow-x: hidden;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: #555;
  text-decoration: none;
}
a:focus {
  outline: none;
}
legend {
  position: absolute;
  top: -9999px;
  width: 0;
  height: 0;
}
label,
input[type="submit"],
input[type="image"],
input[type="reset"],
button {
  cursor: pointer;
}
input[type="radio"]:focus {
  outline: none;
}
.cf:after {
  content: "";
  display: block;
  clear: both;
}
.fl {
  float: left
}
.fr {
  float: right
}
