#app {
  padding-bottom: 40px;
}
#appHeader {
  position: fixed;
  width: 100%;
  padding: 15px 14px;
  background: #292929;
  z-index: 10;
}
#appHeader .inner:after {
    display: block;
    clear: both;
    content: "";
}
#appHeader.active:after {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 60px;
    background: rgba(0, 0, 0, 0.7);
    content: "";
}
#appHeader.active .side_menu {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
#appHeader.active .btn_menu {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
#appHeader .app_logo {
    float: left;
    padding-top: 4px;
}
#appHeader .app_logo img {
      display: inline-block;
}
#appHeader .btn_menu {
    float: right;
    width: 30px;
    height: 30px;
    margin-right: -7px;
    -webkit-transition: cubic-bezier(0.23, 1, 0.32, 1) 0.5s;
    transition: cubic-bezier(0.23, 1, 0.32, 1) 0.5s;
    background: none;
    outline: none;
}
#appHeader .btn_menu .ico_menu {
      width: 5px;
      height: 23px;
      background-position: -90px 0;
}
#appHeader .side_menu {
    position: fixed;
    left: 0;
    top: 60px;
    width: 100%;
    height: 100vh;
    -webkit-transition: cubic-bezier(0.23, 1, 0.32, 1) 0.8s;
    transition: cubic-bezier(0.23, 1, 0.32, 1) 0.8s;
    z-index: 11;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
#appHeader .side_menu .wrap_profile {
      overflow: hidden;
      margin-bottom: 20px;
      padding-bottom: 25px;
      border-bottom: 1px solid #ebebeb;
}
#appHeader .side_menu .wrap_profile .wrap_thumb {
        overflow: hidden;
        float: left;
        width: 80px;
        height: 80px;
        margin-right: 20px;
        border-radius: 50%;
        border: 1px solid rgba(0, 0, 0, 0.2);
}
#appHeader .side_menu .wrap_profile .wrap_thumb.no_profile .thumb_img {
          display: block;
          width: 60px;
          margin: 15px auto 0;
}
#appHeader .side_menu .wrap_profile .user_name {
        font-size: 20px;
        line-height: 80px;
}
#appHeader .side_menu .list_menu {
      padding-bottom: 25px;
      border-bottom: 1px solid #ebebeb;
}
#appHeader .side_menu .list_menu li {
        line-height: 50px;
        padding-left: 20px;
}
#appHeader .side_menu .btn_logout {
      float: right;
      margin-top: 30px;
}
#appHeader .side_menu .inner {
      position: absolute;
      top: 0;
      left: 0;
      width: 80%;
      height: 100%;
      padding: 50px 20px;
      background: #fff;
}
#appHeader .search_form {
    position: relative;
    width: calc(100% + 14px);
    height: 0;
    overflow: hidden;
    -webkit-transition: cubic-bezier(0.215, 0.61, 0.355, 1) 0.3s;
    transition: cubic-bezier(0.215, 0.61, 0.355, 1) 0.3s;
}
#appHeader .search_form.on {
      height: auto;
      margin: 20px 0;
}
#appHeader .search_form.on .inp_search {
        width: 100%;
        -webkit-transition-delay: .3s;
                transition-delay: .3s;
}
#appHeader .search_form.on .btn_search {
        opacity: 1;
        -webkit-transition-delay: .5s;
                transition-delay: .5s;
}
#appHeader .search_form .inp_search {
      display: block;
      border: 1px solid #ededed;
      width: 0%;
      height: 40px;
      text-indent: 10px;
      font-size: 15px;
      color: #555;
      outline: none;
      -webkit-transition: cubic-bezier(0.215, 0.61, 0.355, 1) 0.4s;
      transition: cubic-bezier(0.215, 0.61, 0.355, 1) 0.4s;
}
#appHeader .search_form .btn_search {
      position: absolute;
      outline: none;
      right: 10px;
      top: 8px;
      width: 30px;
      height: 30px;
      opacity: .5;
      background: url("https://image.flaticon.com/icons/svg/149/149309.svg") no-repeat;
      background-size: 25px 25px;
      opacity: 0;
      -webkit-transition: cubic-bezier(0.215, 0.61, 0.355, 1) 0.3s;
      transition: cubic-bezier(0.215, 0.61, 0.355, 1) 0.3s;
}
#appNav {
  position: fixed;
  left: 0;
