  background: #292929;
  text-align: center;
  line-height: 41px;
}
.list_post li {
  margin-bottom: 20px;
}
.list_post .link_item {
  display: block;
}
.list_post .wrap_thumb {
  display: block;
  overflow: hidden;
  position: relative;
  max-height: 220px;
  background: #fff;
}
.list_post .wrap_thumb:after {
    position: absolute;
    bottom: 0;
    left: 0;
    top: 0;
    right: 0;
    border: 1px solid rgba(0, 0, 0, 0.2);
    border-bottom: 0;
    content: '';
}
.list_post .wrap_info {
  padding: 15px 15px 20px;
  background: #fff;
  border: 1px solid #e3e3e3;
}
.list_post .tit_post {
  display: -webkit-box;
  overflow: hidden;
  margin: 10px 0 15px;
  font-size: 20px;
  line-height: 125%;
  word-break: keep-all;
  color: #242424;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.list_post .txt_author {
  font-size: 14px;
  margin-right: 5px;
  color: #333;
}
.list_post .desc_post {
  display: -webkit-box;
  overflow: hidden;
  max-height: 54px;
  margin-bottom: 15px;
  font-size: 14px;
  line-height: 18px;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.list_post .desc_post * {
    color: #555;
}
.list_post .desc_post img {
    display: none;
}
.list_post .txt_tag {
  display: block;
  font-size: 13px;
  color: #888;
  margin-bottom: 15px;
}
.list_post .txt_date {
  float: right;
}
.write_post .inp_author {
  display: block;
  height: 40px;
  margin-bottom: 20px;
  border-bottom: 1px solid #ddd;
  width: 100%;
  text-indent: 10px;
  color: #fff;
  font-size: 15px;
  background: transparent;
}
.write_post .inp_author::-webkit-input-placeholder {
    color: #fff;
}
.write_post .wrap_editor {
  margin-bottom: 20px;
}
.write_post .inp_tag {
  display: block;
  width: 100%;
  height: 40px;
  margin-bottom: 10px;
  border-bottom: 1px solid #e3e3e3;
  text-indent: 10px;
}
.write_post .txt_tag {
  display: block;
  font-size: 12px;
  padding-left: 10px;
}
.write_post .btn_post {
  float: right;
  width: 120px;
  height: 40px;
  margin-top: 20px;
  color: #fff;
  background: #292929;
  text-transform: capitalize;
}
.ql-editor img {
  max-width: 80%;
}
li:last-child {
  margin-bottom: 0;
}
.inner {
  overflow: hidden;
  padding-bottom: 40px;
}

